import React from 'react';

function SessionInterval(props){
    function decrease(){
        if(props.sessionDuration === 1){
            return;
        }
        props.decreasingsessionDuration();
    }
    function increase(){
        if(props.sessionDuration >= 60){
            return;
        }
        props.increasingsessionDuration();
    }
    

    return(
        <div>
            <h2>Session Duration</h2>
            <div className="flex justify-around gap-2 mt-3 text-white">
            <button onClick={decrease} className="bg-gray-800 w-7 h-8 rounded-lg"><svg className="w-6 h-6 text-red-500" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 13l-7 7-7-7m14-8l-7 7-7-7" /></svg></button>
            <p>{props.sessionDuration}</p>
            <button onClick={increase} className="bg-gray-800 w-7 h-8 rounded-lg"><svg className="w-6 h-6 text-red-500" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 11l7-7 7 7M5 19l7-7 7 7" /></svg></button>
        </div>
        </div>
        
    );
}

export default SessionInterval;