import React from 'react';

import './App.css';
import BreakInterval from './BreakTime';
import Pomodoro from './Pomodoro';
import SessionInterval from './SessionTime';





class App extends React.Component {
  constructor(){
    super();
    this.state = { 
      breakDuration : 5,
      sessionDuration : 25,
      timerMinute : 25
    };
    this.increasingbreakDuration = this.increasingbreakDuration.bind(this)
    this.decreasingbreakDuration = this.decreasingbreakDuration.bind(this)
    this.increasingsessionDuration = this.increasingsessionDuration.bind(this)
    this.decreasingsessionDuration = this.decreasingsessionDuration.bind(this)
    this.onToggleInterval = this.onToggleInterval.bind(this)
    this.updatetimerMinute = this.updatetimerMinute.bind(this)
    this.resetTimer = this.resetTimer.bind(this)
  };

 increasingbreakDuration(){
   this.setState((prevState) => {
     return{
       breakDuration : prevState.breakDuration + 1
     }
   });
 }
 decreasingbreakDuration(){
  this.setState((prevState) => {
    return{
      breakDuration : prevState.breakDuration - 1
    }
  });
}
increasingsessionDuration(){
  this.setState((prevState) => {
    return{
      sessionDuration : prevState.sessionDuration + 1,
      timerMinute: prevState.sessionDuration + 1

    }
  });
}
decreasingsessionDuration(){
  this.setState((prevState) => {
    return{
      sessionDuration : prevState.sessionDuration - 1,
      timerMinute: prevState.sessionDuration - 1
    }
  });
}

updatetimerMinute(){
  this.setState((prevState) => {
    return{
      timerMinute: prevState.timerMinute - 1
    }
  });
}

onToggleInterval(isMeeting){
  if (isMeeting){
    this.setState({
      timerMinute : this.state.sessionDuration
    })
  }else {
    this.setState({
      timerMinute : this.state.breakDuration
    })
  }
}

resetTimer(){
  this.setState({
    timerMinute : this.state.sessionDuration
  })
}



  render(){

    return (
      <div className="App">
          <div className="flex flex-col justify-center items-center h-screen "> 
              <div className=" w-2/3 bg-red-500 flex flex-col h-4/6 rounded-lg items-center">
                <div className="pomodoro w-full">
                  <h2 className="w-full  p-5 left-3 top-3 text-white text-lg">Pomodoro App</h2>
                </div>
                <div className="flex mt-4 w-4/6 justify-around">
                  <BreakInterval breakDuration = {this.state.breakDuration}
                  increasingbreakDuration = {this.increasingbreakDuration} decreasingbreakDuration = {this.decreasingbreakDuration} ></BreakInterval>
                  <SessionInterval sessionDuration = {this.state.sessionDuration} increasingsessionDuration = {this.increasingsessionDuration} decreasingsessionDuration = {this.decreasingsessionDuration} ></SessionInterval>
                </div>
                <div>
                  <Pomodoro timerMinute = {this.state.timerMinute}
                  breakDuration = {this.state.breakDuration} updatetimerMinute = {this.updatetimerMinute} toggleIntveral = {this.onToggleInterval} resetTimer = {this.resetTimer} ></Pomodoro>
                </div>
              </div>
          </div>
      </div>
    );
  }
  
  
}

export default App;
