import React from 'react';

class Pomodoro extends React.Component{
    constructor(){
        super();

        this.state = {
            isMeeting : true,
            timerSecond: 0,
            intervalId : 0
        };
        this.start = this.start.bind(this);
        this.decreaseTimer = this.decreaseTimer.bind(this);
        this.pause = this.pause.bind(this);
        this.reset = this.reset.bind(this);
    }

    start(){
        let intervalId = setInterval( this.decreaseTimer, 1000);
        this.setState({
        intervalId : intervalId
        })
    }

    decreaseTimer(){
        
        switch(this.state.timerSecond){
         case 0:
             this.props.updatetimerMinute(); 
             this.setState({
                 timerSecond: 59
             })
             break;
        default:
            this.setState((prevState)=>{
             return{
                    timerSecond : prevState.timerSecond -1
                    }
             })
             break;
        }
    }

    pause(){
        clearInterval(this.state.intervalId)
    }

    reset(){
        this.pause();
        this.props.resetTimer();
        this.setState({
            timerSecond:0
        })
    }

    render(){
        return(
            <div>
                <div className="mt-5 text-lg">
                    <h2>{this.state.isMeeting === true ? 'Meeting in progress': 'Its break time !!!'}</h2>
                    <div className="text-6xl text-white mt-7 bg-gray-800 rounded-lg p-3 flex justify-center">
                        <span>{this.props.timerMinute}</span>
                        <span>:</span>
                        <span>{this.state.timerSecond === 0 ? '00' : this.state.timerSecond < 10 ? '0' +
                         this.state.timerSecond : this.state.timerSecond }</span>
                    </div>
                </div>
                <div className="w-60 flex justify-around mt-10  ">
                    <button onClick = {this.start} className="text-white  bg-gray-800 rounded-lg p-3 ">Start</button>
                    <button onClick = {this.pause}  className="text-white  bg-gray-800 rounded-lg p-3 ">Pause</button>
                    <button onClick = {this.reset}  className="text-white  bg-gray-800 rounded-lg p-3  ">Reset</button>
                </div>
            </div>

        );
    }
}

export default Pomodoro;